.n-wrapper {
  height: 10vh;
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  top: 0;
  
}

.n-left {
  flex: 1;
  align-items: center;
  justify-content: left;
  display: flex;
  gap: 2rem;
}

.n-name {
  font-size: 1.6rem;
  font-weight: bold;
  color: var(--yellow);
  
}
.n-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-weight: 400;
}

.n-list {
  flex: 10;
  color: greenyellow;
}

.n-list > ul {
  display: flex;
  gap: 1rem;
  margin-right: 4rem;
  
}


.n-list > ul :hover {
  color: var(--orange);
  cursor: pointer;
}

.n-button {
  flex: 2;
  width: 6rem;
  height: 2rem;
  margin-top: 0.3rem;
  left: -3.5rem;
  position: relative;
}


@media screen and (max-width: 480px) {
  .n-list {
    display: none;
  }
}
