.Tools  {
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 30rem;
    margin-bottom: 8rem;
    margin-top: 9rem;
    margin-bottom: 13rem;
    /* scroll view */
    padding-top: 3rem;
    
  }
  /* left side */
  .killer {
    display: flex;
    flex-direction: column;
    position: relative;
    
    
    /* gap: 20px; */
  }
  
  .killer > :nth-child(1) {
    color: var(--black);
    font-size: 1rem;
    font-weight: normal;
    margin-top: 5rem;
    
    
  }
  
  .d-button {
    width: 10rem;
    height: 2rem;
    margin-top: 1rem;

     
  }
 
  /* right sided */
  
  .f-right {
    position: absolute;
    right: -400px;
    width: 300px;
    padding: 10px;
    left: 200%;
    border-radius: 50%;

  
    
    
  }
  .g-right  {
    position: absolute;
    right: -400px;
    width: 300px;
    padding: 10px;
    margin-top: -33rem;
    
    
    
    
    
  }
  /* blur */
  .s-blur2 {
    left: 14rem;
    top: 8rem;
  }
  
  .s-blur1 {
    top: 13rem;
    left: -18rem;
  }
  
  @media screen and (max-width: 480px) {
    .Tools {
      margin-top: 0;
      flex-direction: column;
      gap: 5rem;
      height: 66rem;
      padding: 0;
    }
  
    
  }