.Intro {
  display: flex;
  height: 77vh;
  margin-top: 6rem;
}
.z-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-weight: 400;
  left: 24%;
  position: relative;
}

.z-list {
  flex: 10;
  color: greenyellow;
  margin-top: -87rem;
  left: -140%;
  position: relative;
}

.z-list > ul {
  display: flex;
  gap: 1rem;
  margin-right: 4rem;
  
}


.z-list > ul :hover {
  color: var(--orange);
  cursor: pointer;
}

/* left side styling */
.i-left {
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column;
  gap: 2rem;
}
.i-name {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  left: -70%;
  position: relative;
  margin-top: 7rem;
}
.i-name > :nth-child(1) {
  color: var(--orange);
  font-weight: bold;
  font-size: 3rem;
  width: 15rem;
  left: 70%;
  position: relative;

}
.i-name > :nth-child(2) {
  color: var(--orange);
  font-weight: bold;
  font-size: 3rem;
  left: 70%;
  position: relative;
  
}
.i-name > :nth-child(3) {
  font-weight: 100;
  font-size: 20px;
  color: var(--blueCard);
  margin-top: 10px;
  left: 70%;
  position: relative;
}
.i-button {
  width: 6rem;
  height: 2rem;
  left: -6%;
  position: relative;
}
.i-icons {
  margin-top: 0.5rem;
  display: flex;
  gap: 0rem;
  left: -10%;
  position: relative;
  
}
.i-icons > * {
  transform: scale(0.5);
  
}
.i-icons > *:hover {
  cursor: pointer;
}
/* right side styling */
.i-right {
  flex: 1;
  position: relative;
}
.i-right > * {
  position: absolute;
  z-index: 1;
}
.i-right > :nth-child(1) {
  transform: scale(0.6);
  left: -23%;
  width: 1800px;
  height: 1100px;
  margin-top: -16rem;
  position: relative;
}
.i-right > :nth-child(2) {
  top: -2rem;
  transform: scale(1.3);
  left: -15rem;
  height: 110px;
  width: 240px;
}
.i-right > :nth-child(3) {
  transform: scale(0.5);
  left: -35%;
  top: 2rem;
  
}
.i-right > :nth-child(4) {
  transform: scale(0.62);
  top: -19%;
  left: -24%;
  border-radius: 50%;
  padding: 0;
}

/* blur */
.blur {
  position: absolute;
  width: 27rem;
  height: 18rem;
  border-radius: 50%;
  background: #020003;
  filter: blur(212px);
  z-index: -9;
  top: -18%;
  left: 56%;
  filter: blur(72px);
}

@media screen and (max-width: 480px) {
  .Intro {
    flex-direction: column;
    gap: 7rem;
    height: 64rem;
  }

  .i-right {
    transform: scale(0.8);
    left: -3rem;
  }
  .i-right .blur {
    display: none;
  }
  .floating-div:nth-of-type(1){
    /* display: none; */
    top: -7rem !important;;
  }
}
